var APP = document.querySelector('.app');
export function resaultGame(game, resault) {
    var wrap = document.createElement("div");
    wrap.classList.add("wrap");
    // Создание общей обертки
    var chooseWrap = document.createElement("div");
    chooseWrap.classList.add("choose-wrap", "resault");
    // Создание картинки 
    var chooseImg = document.createElement("div");
    chooseImg.classList.add("choose-img");
    chooseImg.style.backgroundImage = "url(\"".concat(resault, "\")");
    // Создание Title
    var h2 = document.createElement("h2");
    h2.classList.add("choose__title", "choose__title-resault");
    h2.textContent = "\u0412\u044B ".concat(game, "!");
    // Создание описания таймера
    var chooseTimer = document.createElement("p");
    chooseTimer.classList.add("choose-time");
    chooseTimer.textContent = "Затраченное время:";
    // Получение значения Таймера
    var valueSeconds = document.querySelector('.timers__seconds').innerHTML;
    var valueMinutes = document.querySelector('.timers__minutes').innerHTML;
    // Создание таймера
    var timer = document.createElement("div");
    timer.classList.add("timer");
    timer.innerHTML = "".concat(valueMinutes, ".").concat(valueSeconds);
    // Создание Кнопки
    var btnChoose = document.createElement("button");
    btnChoose.classList.add("choose__btn");
    btnChoose.textContent = "Играть снова";
    // Создание общей обертки для ссылок
    var chooseLinkBg = document.createElement("div");
    chooseLinkBg.classList.add("choose__link-bg");
    APP.before(wrap);
    wrap.appendChild(chooseWrap);
    chooseWrap.appendChild(chooseImg);
    chooseWrap.appendChild(h2);
    chooseWrap.appendChild(chooseTimer);
    chooseWrap.appendChild(timer);
    // Добавление кнопки
    chooseWrap.appendChild(btnChoose);
    btnChoose.addEventListener('click', function (e) {
        e.preventDefault();
        window.location.reload();
    });
}
